.catalog {
    margin-top: 100px;

    .title {
        font-family: "MulishSemiBold";
        font-size: 46px;
        margin-bottom: 60px;
        text-align: center;
    }

    .catalogWrapper {
        display: flex;
        gap: 15px;
        justify-content: space-between;
    }

    .btnWrapper {
        margin-top: 55px;
        text-align: center;

        .btn {
            background-color: unset;
            border: 1px solid #000000;
            border-radius: 30px;
            font-size: 20px;
            outline: none;
            padding: 12px 18px;
            cursor: pointer;
        }
    }    
}